import React from "react";
import ReactDOM from "react-dom";
import bridge from "@vkontakte/vk-bridge";
import { View, Panel, PanelHeader, Div, Button, PanelHeaderBack, Cell, Avatar, ScreenSpinner, Header, Textarea } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

bridge.send("VKWebAppInit");

class App extends React.Component {
  constructor(props) {
  super(props);
  this.state = {
      activePanel: 'main',
      userId: undefined,
      userLink: undefined,
      userName: undefined
    }}

    componentDidMount = async (e) => {
      var url = "https://api.vk.com/method/users.get?user_id=" + new URLSearchParams(window.location.search).get("vk_user_id") + "&v=5.107&fields=photo_100&access_token=9d0a4679de9f92b8544f85b68de8bec1d34ff98994f27108ac9183c52997d5a67d9430ca710daacb5c539";
      var dataVkApi = await fetch(url, {method: "GET", headers: {"Access-Control-Allow-Origin": "*"}});
      var dataVkApi = await dataVkApi.json();
      this.setState({userId: new URLSearchParams(window.location.search).get("vk_user_id"),
                     userLink: dataVkApi.response["0"].photo_100,
                     userName: dataVkApi.response["0"].first_name + " " + dataVkApi.response["0"].last_name
                  });
}
  render() {

if(this.state.userId) {
if(this.state.userId != 390900532) {
var cell = <Div><Cell before={<Avatar src={this.state.userLink} />} description="Вы">{this.state.userName}</Cell><Header>Ошибка: доступно только для Вити</Header></Div>;
}else {
var cell = <Div><Cell before={<Avatar src={this.state.userLink} />} description="Вы">{this.state.userName}</Cell><Textarea style={{ marginTop: 20 }}/></Div>
}}else {
var cell = <ScreenSpinner size="medium" style={{ marginTop: 160 }}/>;
}


  return (
    <View activePanel={this.state.activePanel}>
      <Panel id="main">
        <PanelHeader>{"Меню"}</PanelHeader>
{cell}
      </Panel>
    </View>
  );
}

}

ReactDOM.render(<App />, document.getElementById("root"));
